<template>
  <v-main class="d-flex align-center Wrapper">
    <v-container fluid class="d-flex justify-center align-center">
      <v-card width="100%" max-width="700px" outlined>
        <v-card-title> </v-card-title>
        <v-card-text>
          <v-text-field
            v-model="Name"
            :error-messages="nameErrors"
            @input="$v.Name.$touch()"
            @blur="$v.Name.$touch()"
            label=".."
          ></v-text-field>

          <v-text-field
            v-model="UserName"
            :error-messages="usernameErrors"
            @input="$v.UserName.$touch()"
            @blur="$v.UserName.$touch()"
            label=""
          ></v-text-field>

          <v-text-field
            v-model="Password"
            :error-messages="passwordErrors"
            label=""
            @input="$v.Password.$touch()"
            @blur="$v.Password.$touch()"
          ></v-text-field>
          <v-text-field
            v-model="Email"
            :error-messages="emailErrors"
            @input="$v.Email.$touch()"
            @blur="$v.Email.$touch()"
            label=" "
          ></v-text-field>
          <v-checkbox v-model="checkbox" label=" ?" />
        </v-card-text>
        <v-card-actions>
          <v-btn @click="save"></v-btn>
          <v-btn @click="$router.go(-1)"></v-btn>
        </v-card-actions>
      </v-card>
    </v-container>
  </v-main>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import { required, email } from "vuelidate/lib/validators";
export default {
  metaInfo: {
    title: "Edit User",
  },
  data() {
    return {
      checkbox: false,
    };
  },
  mixins: [validationMixin],
  validations: {
    Name: { required },
    UserName: { required },
    Password: { required },
    Email: { required, email },
  },
  computed: {
    nameErrors() {
      const errors = [];
      if (!this.$v.Name.$dirty) return errors;
      !this.$v.Name.required && errors.push("... ");
      return errors;
    },
    usernameErrors() {
      const errors = [];
      if (!this.$v.UserName.$dirty) return errors;
      !this.$v.UserName.required && errors.push(" ");
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.Password.$dirty) return errors;
      !this.$v.Password.required && errors.push(" ");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.Email.$dirty) return errors;
      !this.$v.Email.email &&
        errors.push("    ");
      !this.$v.Email.required && errors.push("  ");
      return errors;
    },
    Name: {
      get() {
        return this.getUserName();
      },
      set(value) {
        console.log(this.$v);
        this.setUserName(value);
      },
    },
    UserName: {
      get() {
        return this.getUserLogin();
      },
      set(value) {
        this.setUserLogin(value);
      },
    },
    Email: {
      get() {
        return this.getUserEmail();
      },
      set(value) {
        this.setUserEmail(value);
      },
    },
    Password: {
      get() {
        return this.getUserPassword();
      },
      set(value) {
        this.setUserPassword(value);
      },
    },
    SetAdmin: {
      get() {
        return this.getUserAdmin();
      },
      set(value) {
        if (this.checkbox) {
          this.setUserAdmin(value);
        }
      },
    },
  },
  methods: {
    ...mapActions({
      uploadUserProfile: "profile/SET_CURRENT_USER",
      editUser: "profile/EDIT_CURRENT_USER",
    }),
    ...mapGetters({
      getUserName: "profile/GET_NAME",
      getUserLogin: "profile/GET_USERNAME",
      getUserEmail: "profile/GET_EMAIL",
      getUserPassword: "profile/GET_PASSWORD",
      getUserAdmin: "profile/GET_ADMIN",
    }),

    ...mapMutations({
      setUserName: "profile/SET_USER_NAME",
      setUserLogin: "profile/SET_USER_USERNAME",
      setUserEmail: "profile/SET_USER_EMAIL",
      setUserPassword: "profile/SET_USER_PASSWORD",
      setUserAdmin: "profile/SET_USER_ADMIN",
    }),
    save() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        console.log("111");
        this.appAlert(
          "",
          "    !",
          "error"
        );
        return;
      }
      this.editUser().then((res) => {
        if (!res.success) {
          this.appAlert(" ", res.msg, "error");
          return;
        } else {
          this.appAlert(" ", res.msg, "success");
          this.$router.go(-1);
        }
      });
    },
  },
  created() {
    this.uploadUserProfile(this.$route.params.id);
  },
};
</script>
<style scoped lang="scss">
.Wrapper {
  background-image: url("../assets/images/user/Sun-Tornado.svg");
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
}
</style>
